.rotateOut {
    -webkit-animation: rotateOut 1s .2s ease both;
    -moz-animation: rotateOut 1s .2s ease both
}

@-webkit-keyframes rotateOut {
    0% {
        -webkit-transform: rotate(0);
        opacity: 1
    }
    100% {
        -webkit-transform: rotate(-90deg);
        opacity: 1
    }
}

